*{
    margin: 0; padding: 0; font-family:sans-serif;
}



.navbar
{
    display: flex; justify-content: space-around; position: sticky;
    top:0; 
    padding: 10px; width: 100%; box-sizing: border-box; flex-wrap: wrap; align-items: center;
  background: #fff;
  z-index: 4;
 

}


.navbar #logo
{
    font-size: 1.4em;
    font-weight: 600;
    cursor: pointer;

 
}


.navbar #logo span
{
    color:transparent;
    background-image: linear-gradient(-60deg,#ff0066,orangered);
    -webkit-background-clip: text;
    font-weight: 600;
}


.navbar #nav--links
{
    display: flex; 
 flex-wrap: wrap; align-items: center;
 justify-content: space-around;
}



.navbar #nav--links a
{
    margin-left: 10px; margin-right: 10px;
    text-transform:capitalize;
    font-size: 1em;
    font-weight: 600;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    text-overflow: nowrap;
   
  
}

.navbar #nav--links a::after{
    position: absolute;
    content:attr(data-text);
    left:0;
    top:100%;
    color:transparent;
    background-image: linear-gradient(-60deg,#ff0066,orangered);
    -webkit-background-clip: text;
    transition: 0.5s ease;


}


.navbar #nav--links a:hover,
.navbar #nav--links .active{
    color: transparent;
}

.navbar #nav--links a:hover::after,
.navbar #nav--links .active::after
{
    top:0;
}

#home
{
    display: flex; flex-direction: column; justify-content: center; align-items: center;
    min-height:100vh;
    width: 100%; 
    background: lightyellow;
  

}

#home label
{
    font-size: 1.2em;
    margin-top: 40px;
}

#home h1
{
    font-size: 3.5em;
    margin-top: 10px;
}

@media screen and (max-width:600px)
{
    .navbar #nav--links
    {
        width: 100%;
        margin-top: 10px;
    }
    
#home h1
{
    font-size: 2.5em;
}

}

#home h1 span{
    color:transparent;
    background-image: linear-gradient(-60deg,#ff0066,orangered);
    -webkit-background-clip: text;
}
#home #type {font-size: 1.4em; margin-top: 10px; position: relative; color: transparent;}

#home #type:after
{
	content: attr(data-text);
	position: absolute;
	height: 100%;
	width: 100%;
	left:0;
	top:0;
	color: #000;

	border-right: 2px solid #ff0066;
  overflow: hidden;
  white-space: nowrap;
  animation:type 3s steps(12) infinite;
}

@keyframes type
{
	0%
	{
		width: 0;
	}
	60%
	{
		width: 100%;
    }
	100%
	{
		width: 100%;
	}
}


#home .socialIcons
{
    position: absolute;
    height: 130px;
    display: flex;
    flex-direction:column;
    top:calc(50% - 65px);
    z-index:5;
    left:0;

}

#home .socialIcons li
{
  display:flex;
  width: 160px;
  height:30px;
  position: relative;
  margin-top: 10px;
  background: #fff;
  border-radius: 0 40px 40px 0;
  left:-130px;
  justify-content: space-between;
  transition: 0.5s;   
}

#home .socialIcons li a{
    width: 120px;
    height:100%;
    line-height:30px;
    display:block;
    text-align: center; 
    text-decoration: none;
    font-size: 1em;
   font-weight: 600;
    
}

#home .socialIcons li i
{
    height: 30px;
    width: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    border-radius:50%;
    font-size: 1.2em;
    border:2px solid #fff;
    box-sizing: border-box;
    transition: 0.5s;  
}

#home .socialIcons li:hover
{
    left:0;
}

#home .socialIcons li:hover i
{
    transform: rotate(360deg);
}

#home .profile--img
{
    height: 200px;
    width: 200px;
    position: relative;
}
#home .profile--img img { width: 100%; height: 100%; object-fit: cover; position:absolute; top:0; left:0; z-index: 2;}
#home .profile--img .line--border
{
    position:absolute; width: 100%; height: 100%;
    left:0; top:0; transform: rotate(30deg);
    box-sizing: border-box;
     border-left:8px solid orangered;
     border-right:8px solid #ff0066;
     z-index: 1;
   
}


#home .profile--img .cover--border
{   
 
    position:absolute; width:100%; height: 100%;
    left:0; top:0; 
    box-sizing: border-box;
     border-bottom:8px solid orangered;
     border-top:8px solid #ff0066;
     transform: rotate(30deg);
     z-index: 3;
}

#home #hire
{
    margin-top: 20px;
    text-decoration: none;
    background:#000;
    height: 30px; width: 80px;
    line-height: 30px;
    color:transparent;
    color:lightyellow;
    text-align: center;
    box-shadow:0 0 2px rgba(0,0,0.5);
    font-weight: 500;
    text-shadow: 0.5px 0.5px 1px rgba(0,0,0,.5);
}

.section
{
    display: flex; flex-direction: column; justify-content: center; align-items: center;
    min-height:100vh;
    width: 100%; 
}

#about h1
{
    font-size: 3em;
    margin-top: 40px;
}
#about .about-contents
{
    display: flex;
    width: 80%;
    flex-wrap: wrap;
    margin-top: 100px;
}

#about .about-contents .summary
{
    width: 50%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
}

#about .about-contents div .image--box
{
    height: 300px;
    width:250px;
    position: relative;
}

#about .about-contents .image
{
    align-items: center;
    width: 50%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

#about .about-contents div .image--box img
{
    position: absolute; height: 100%; width: 100%; object-fit: cover;
   z-index: 2;
   left:4px;
   top:-12px;
    box-sizing: border-box;
}

#about .about-contents .image--box::after{
    content:'';   position: absolute; height: 50%; width: 100%; 
    top:4px; left:-1px; background:rgba(0,0,0,.3);
    transform: skewX(5deg);
    transform-origin:bottom;
    z-index: 1;
    filter: blur(3px);
}

#about .about-contents .image--box::before{
    content:'';   position: absolute; height: 50%; width: 100%; 
    bottom:0; left:-1px; background:rgba(0,0,0,.3);
    transform: skewX(-5deg);
    transform-origin:top;
    z-index: 1;
    filter: blur(3px);
}

#about .about-contents .summary h1
{
    font-size: 1.7em;
}


#about .about-contents .summary span
{
    margin-top:5px;
    color:transparent;
    background-image: linear-gradient(-60deg,#ff0066,orangered);
    -webkit-background-clip: text;
    font-weight: 600;

}
 
#about .about-contents .summary h2
{
    margin-top:20px;
    font-size: 1.3em;
} 

#about .about-contents .summary p {
    float: left;
    line-height: 25px;
    margin-top: 20px;
    font-size: 1em;
    width: 100%;
    font-weight: 600;
    overflow-wrap: break-word;
}
#about .about-contents .summary p::first-letter
{
    color:transparent;
    background-image: linear-gradient(-60deg,#ff0066,orangered);
    -webkit-background-clip: text;
    font-weight: 600;
    font-size: 4em;
}

#about .about-contents .summary .button{
margin-top:20px;
width: 100px;
height: 30px;
border:none;
outline: none;
color:transparent;
text-decoration: none;
background:#000;
position:relative;
cursor: pointer;

}
#about .about-contents .summary .button::after{
     content:'More'; position:absolute;
     height: 100%;
      width: 100%;
      background-image: linear-gradient(-60deg,#ff0066,orangered);
     left:4px;
     top:-4px;
     color: #fff;
     text-align: center;
     line-height: 30px;
     font-weight: 600;
     text-shadow: 0.5px 0.5px 1px #000;
     transition: 0.5s;
    }

    #about .about-contents .summary .button:hover::after
    {
        top: 0;
        left:0;
    }
 @media screen and (max-width:600px)
{
#about .about-contents .summary
{
    width: 100%;
}
#about .about-contents .image
{
    width: 100%;
}
}

#skills h1
{
    font-size: 3em;
    margin-top: 30px;

}

.objective--strength
{
    display:grid;
    grid-template-columns:repeat(auto-fit,minmax(200px,1fr)); 
    width: 80%;
    grid-gap: 10px;
    margin-top: 40px;

    
}

.objective--strength .box
{
    padding: 20px;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    background:#111;
    box-sizing: border-box;
    box-shadow: 0 0 4px rgba(0,0,0,.5);
    height: 300px;
}
.objective--strength .box .fas
{
   
    font-size: 5em;
    color:#fff;
}
.objective--strength .box h3
{
    margin-top: 10px;
    color:transparent;
    background-image: linear-gradient(-60deg,#ff0066,orangered);
    -webkit-background-clip: text;
    font-size:2em;
}
.objective--strength .box p
{
    margin-top: 10px;
    font-size: 1.2em;
    color:#fff;
}